						svg
							use(xlink:href="@/assets/img/messenger/icons/sprite.svg#paperclip")

				input.chat-messenger__field(type="text" placeholder="–í–∞—à —Å–æ–æ–±—â–µ–Ω–∏–µ..." v-model="newMessage")
